import React, { useState, useEffect } from "react";
import NavbarServiceCenter from "./NavbarServiceCenter";
import {
  Checkbox,
  FormControl,
  InputLabel,
  ListItemText,
  MenuItem,
  Select,
  CircularProgress,
  IconButton,
  Button,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from "@mui/material";
import { useNavigate, useParams } from "react-router-dom";
import axios from "axios";
import DeleteIcon from "@mui/icons-material/Delete";
import { useHistory } from "react-router-dom";

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 250,
    },
  },
};

const faultCategories = {
  "Brake Issues": {
    "Brake Pads": 1500,
    "Brake fluid leaks": 655,
    "Brake overheating": 12000,
    "Brake pedals": 600,
    "Brake warning lights": 900,
    "Steering wheel alignment": 1700,
    "Brake fade": 960,
  },
  "Engine Overheating": {
    "Thermostat Correction": 3200,
    "Water pump failure": 2360,
    "Defective fan": 3200,
    "Radiator problems": 1800,
    "Low coolant": 1200,
    "Cooling system": 5600,
    Pistons: 10000,
  },
  "Transmission Failure": {
    "Diagnistic Assessment": 600,
    "Fluid Check and Replacement": 1500,
    "Transmission Components": 960,
    "Electronic control system check": 998,
    "Seals and Gaskets": 980,
    "Transmission rebuild": 956,
  },
  "Suspension Problems": {
    Springs: 6200,
    "Shock absorbers": 5200,
    "Ball joints": 3000,
    Struts: 3500,
    "Control arms": 3200,
    "Sway bar": 2600,
    "Sway bar links": 2600,
  },
  "FC Service": {
    "Fuel filter": 1000,
    "Fuel pump": 6200,
    "Fuel injectors": 1200,
    "Fuel pressure": 1600,
    "Fuel pressure regulator": 2600,
    "Fuel tank": 2600,
    "Fuel tank cap": 550,
    Painting: 16000,
    Tinkering: 8956,
  },
  "Electrical Faults": {
    Battery: 9600,
    Alternator: 3200,
    Starter: 6300,
    "Ignition coil": 700,
    "Spark plugs": 300,
    "Spark plug wires": 300,
  },
  Other: {
    "Brake Pads": 1500,
    "Brake fluid leaks": 655,
    "Brake overheating": 12000,
    "Brake pedals": 600,
    "Brake warning lights": 900,
    "Steering wheel alignment": 1700,
    "Brake fade": 960,
    "Thermostat Correction": 3200,
    "Water pump failure": 2360,
    "Defective fan": 3200,
    "Radiator problems": 1800,
    "Low coolant": 1200,
    "Cooling system": 5600,
    Pistons: 10000,
    "Diagnistic Assessment": 600,
    "Fluid Check and Replacement": 1500,
    "Transmission Components": 960,
    "Electronic control system check": 998,
    "Seals and Gaskets": 980,
    "Transmission rebuild": 956,
    Springs: 6200,
    "Shock absorbers": 5200,
    "Ball joints": 3000,
    Struts: 3500,
    "Control arms": 3200,
    "Sway bar": 2600,
    "Sway bar links": 2600,
    "Fuel filter": 1000,
    "Fuel pump": 6200,
    "Fuel injectors": 1200,
    "Fuel pressure": 1600,
    "Fuel pressure regulator": 2600,
    "Fuel tank": 2600,
    "Fuel tank cap": 550,
    Painting: 16000,
    Tinkering: 8956,
    Battery: 9600,
    Alternator: 3200,
    Starter: 6300,
    "Ignition coil": 700,
    "Spark plugs": 300,
    "Spark plug wires": 300,
  },
};

const BillGeneration = () => {
  const sessionId = localStorage.getItem("sessionId");
  const { faultInfo, serviceId, customerId } = useParams();
  const navigate = useNavigate();

  const [selectedServices, setSelectedServices] = useState([]);
  const [customer, setCustomer] = useState(null);
  const [loading, setLoading] = useState(true);
  const [formData, setFormData] = useState({
    billEstimation: 0,
    date: "",
    description: "", // Updated: added description field
    status: "PAID",
  });

  useEffect(() => {
    const fetchCustomerData = async () => {
      try {
        const response = await axios.get(
          `http://localhost:5921/serviceRequest/getRequestbyId/${serviceId}`
        );
        setCustomer(response.data);
        setLoading(false);
      } catch (error) {
        console.error("Error fetching:", error);
        setLoading(false);
      }
    };
    fetchCustomerData();
  }, [serviceId]);

  useEffect(() => {
    const totalAmount = selectedServices.reduce(
      (total, service) => total + faultCategories[faultInfo][service],
      0
    );
    setFormData((prev) => ({
      ...prev,
      billEstimation: totalAmount,
      description: selectedServices.join(", "), // Updated: set description to selected services
    }));
  }, [selectedServices, faultInfo]);

  const handleUpdate = async (e) => {
    e.preventDefault();
    const currentDate = new Date().toLocaleString();
    setFormData((prev) => ({ ...prev, date: currentDate }));
    try {
      customer.status = "RESOLVED";
      await axios.put(
        `http://localhost:5921/serviceRequest/update/${serviceId}?scId=${sessionId}`,
        customer
      );
      await axios.post(
        `http://localhost:5921/bill/create?cId=${customerId}&scId=${sessionId}&srId=${serviceId}`,
        { ...formData, date: currentDate }
      );
      alert("Bill Generated");
      navigate(-1);
    } catch (error) {
      console.error("Error updating:", error);
    }
  };

  const handleSelectChange = (event) => {
    const { value } = event.target;
    setSelectedServices(value);
  };

  const handleDelete = (index) => {
    setSelectedServices((prevSelected) =>
      prevSelected.filter((_, i) => i !== index)
    );
  };

  // Filter out the selected services from the list of available options
  const availableOptions = Object.keys(faultCategories[faultInfo]).filter(
    (name) => !selectedServices.includes(name)
  );

  return (
    <div>
      <NavbarServiceCenter />
      <br />
      <div className="bill-generation-container">
        <h1>Generate Bill</h1>
        {loading ? (
          <CircularProgress />
        ) : (
          <div className="form-container">
            <form onSubmit={handleUpdate} className="bill-form">
              <FormControl
                sx={{ m: 1, width: "100%" }}
                className="form-control"
                style={{ width: "300px" }}
              >
                <InputLabel style={{ color: "black" }}>Select Service</InputLabel>
                <Select
                  style={{ width: "300px" }}
                  label="Select Service"
                  id="demo-multiple-checkbox"
                  multiple
                  value={selectedServices}
                  onChange={handleSelectChange}
                  MenuProps={MenuProps}
                >
                  {availableOptions.map((name) => (
                    <MenuItem key={name} value={name}>
                      <Checkbox checked={selectedServices.indexOf(name) > -1} />
                      <ListItemText primary={name} />
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
              <br />
              <Button
                type="submit"
                variant="contained"
                className="generate-bill-button"
              >
                Generate Bill
              </Button>
            </form>
            <TableContainer component={Paper}>
              <Table>
                <TableHead>
                  <TableRow>
                    <TableCell>S.No</TableCell>
                    <TableCell>Description</TableCell>
                    <TableCell>Price</TableCell>
                    <TableCell>Actions</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {selectedServices.map((service, index) => (
                    <TableRow key={index}>
                      <TableCell>{index + 1}</TableCell>
                      <TableCell>{service}</TableCell>
                      <TableCell>${faultCategories[faultInfo][service]}</TableCell>
                      <TableCell>
                        <IconButton onClick={() => handleDelete(index)} size="small">
                          <DeleteIcon />
                        </IconButton>
                      </TableCell>
                    </TableRow>
                  ))}
                  <TableRow>
                    <TableCell colSpan={2}>Total:</TableCell>
                    <TableCell>${formData.billEstimation}</TableCell>
                    <TableCell></TableCell>
                  </TableRow>
                </TableBody>
              </Table>
            </TableContainer>
          </div>
        )}
      </div>
    </div>
  );
};

export default BillGeneration;
